  color: #18FFFF;
  font-family: 'Roboto', sans-serif;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%)
}

.faq-container {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 170px;
}

.faq-container > div{
  flex-grow: 1;
  flex: 50% 1;
}


.faq-container article{
  margin: 10px;
  margin-bottom: 30px;
  background: #010031;
  border: 1px solid #010031;
  box-shadow: 1px 1px 15px rgba(24, 255, 255, 0.5);
  border-radius: 24px;
  position: relative;
  overflow: hidden;
}

.faq-container div{
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: normal;
  padding: 0.1em 0.3em;
  color: #18FFFF;
}

.faq-container p{
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  color: #FFFFFF;
  padding: 0em 0.4em 0em 0.9em;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

.arrow{
  position: absolute;
  top: 0.3em;
  right: 0.3em;
  height: 38px;
  transition: 100ms;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

@media only screen and (max-width: 992px) {

  .faq-container > div{
    flex-grow: 1;
    flex: 100% 1;
  }

  .faq-container{
    margin-top: 280px;
  }

  .faq-container div{
    font-size: 30px;
  }

}

